<template>
	<div id="questionslist">
		<div class="qa_list">
			<div class="qa_list_title">
				<div class="qlt_wrap clearfix">
					<i></i>
					<h2>问答列表</h2>
				</div>
			</div>
			<template v-for="qa in qaList" @show="list">
				<router-link :to="{path: 'questions/'+qa.id+'/'+qa.slug}">
					<div class="qa_list_item clearfix">
						<div class="qli_wrap">
							<div class="question_q clearfix">
								<span>Q</span>
								<p>{{ qa.question }}</p>
							</div>
							<div class="answer_a clearfix">
								<span>A</span>
								<p>{{ qa.repliedAnswer.answer }}</p>
							</div>
						</div>
						<div class="qa_right_number clearfix">
							<p>{{ qa.answer_count }}</p>
							<p>个答案</p>
						</div>
					</div>
				</router-link>
			</template>
			
		</div>
	</div>
</template>

<script>
	export default{
		name: 'questionslist',
		props: {
			categoryId: {
				type: String 
			},
		},
		data() {
			return {
				qaList: []
			}
		},
		methods:{
			list(){
				console.log(this.classId)
			}
		},
		created() {
			this.$axios.get(this.HOST + 'questions?include=repliedAnswer,questionCategory',{
				headers: {
					 Accept: "application/prs.meiweixueyuan.v1+json"
				}
			}).then(res => {
				this.qaList = res.data.data;
				console.log(this.qaList)
			}).catch(error => {
				console.log(error)
			})
		}
	}
</script>

<style>
</style>



