<template>
	<div id="articleshow">
		<div class="news_cont">
			<h1>{{ articleShow.name }}</h1>
			<div class="auther clearfix">
				<span>作者：美味学院</span>
				<span>发布时间：{{ articleShow.created_time_date }}</span>
				<span>浏览量：{{ articleShow.view_count }}</span>
			</div>
			<div class="news_text" v-html="articleShow.body"></div>
			<div class="through_page">
				<p>
					<span>上一篇：</span>
					<a href="http://www.meiweixueyuan.cn/articles/55" target="_blank">烘培学校哪里好 - 美味学院，西点烘培的直营连锁品牌</a>
				</p>
				<p>
					<span>下一篇：</span>
					<a href="http://www.meiweixueyuan.cn/articles/57" target="_blank">学小龙虾技术哪家强 - 美味学院斩获“虾王争霸赛”金奖</a>
				</p>
			</div>
		</div>
		<Hot />
	</div>
</template>

<script>
import Hot from '../soncomponents/hotrecommend.vue';
export default {
	name: 'articleShow',
	data() {
		return {
			articleShow: []
		};
	},
	components:{
		Hot
	},
	created() {
		var id = this.$route.params.id;
		this.getarticleId(id);
		console.log(id);
	},
	methods: {
		getarticleId(id) {
			this.$axios
				.get(this.HOST + 'articles/' + id, {
					headers: {
						Accept: 'application/prs.meiweixueyuan.v1+json'
					}
				})
				.then(res => {
					this.articleShow = res.data;
					console.log(this.articleShow);
				});
		}
	}
};
</script>

<style></style>



