<template>
	<div id="article">
			<div class="tabs">
				<ul>
					<li><a class="active" href="http://www.meiweixueyuan.cn/articles?category_id=1" target="_blank">创业知识</a></li>
					<li><a href="http://www.meiweixueyuan.cn/articles?category_id=2" target="_blank">开店攻略</a></li>
					<li><a href="http://www.meiweixueyuan.cn/articles?category_id=3" target="_blank">学院新闻</a></li>
					<li><a href="http://www.meiweixueyuan.cn/articles?category_id=4" target="_blank">行业动态</a></li>
				</ul>
			</div>
			<div class="new_list">
				<div class="new_item clearfix" v-for="art in articleList">
					<router-link :to="{ path: 'articles/'+ art.id }">
						<div class="new_pic">
							<img :src="art.qiniu_url" :alt="art.title" :title="art.title" />
						</div>
						<div class="new_brief">
							<h2>{{ art.name }}</h2>
							<p>{{ art.intro }}</p>
							<div class="new_icon clearfix">
								<i class="clock"></i>
								<p>{{ art.created_time_date }}</p>
								<i class="eye"></i>
								<p>{{ art.view_count }}</p>
							</div>
						</div>
					</router-link>
				</div>
				
				<div class="turn_pages">
					<a href="javascript:;">上一页</a>
					<a href="javascript:;" class="active">1</a>
					<a href="http://www.meiweixueyuan.cn/articles?page=2">2</a>

					<a href="http://www.meiweixueyuan.cn/articles?page=2">下一页</a>
					<a href="javascript:;">共20页</a>
				</div>
			</div>
	</div>
</template>

<script>
export default {
	name: 'articles',
	data() {
		return {
				articleList:[]
		}
	},
	created() {
		this.$axios.get(this.HOST + 'articles',{
			headers:{
				Accept: 'application/prs.meiweixueyuan.v1+json'
			}
		})
		.then( res=> {
			this.articleList = res.data.data;
			console.log(this.articleList)
		})
	}
};
</script>

<style></style>



