<template>
  <div id="mwxy">
		<Header />
		<keep-alive exclude="courseShow,articleShow,questionShow">
		<router-view  v-if="routerAlive" />
		</keep-alive>
		<Footer />
  </div>
</template>

<script>
import Header from './layouts/header'
import Footer from './layouts/footer'
export default {
	name: 'mwxy',
	components:{
		Header,
		Footer,
	},
	data(){
		return {
			zxlink: 'http://mwxy.zoosnet.net/LR/Chatpre.aspx?id=MWT76584867&amp;lng=cn',
			routerAlive:true,
			//key: ''
		}
	},
	watch:{

　　　'$route'(to,from){

　　　　　　this.isRouterActive = false;
　　　　　//　this.key = Math.random()*1000;
　　　　　　this.$nextTick(()=>(this.isRouterActive = true));
			window.location.reload()
		}

　　},
	methods:{
		 // routerRefresh(){
			// this.routerAlive = false;
			// this.$nextTick(()=>{
		 //    this.routerAlive = true;
	  //       });
	  //   }

	}
}
</script>

<style lang="less">
	#mwxy{
		padding-top: .85rem;
		}
	@import "./style/style";
</style>




