import Home from '@/components/home'
import Courses from '@/components/courses/courses'
import courseShow from '@/components/courses/show'
import Question from "@/components/questions/questions"
import questionShow from "@/components/questions/show"
import Articles from "@/components/articles/articles"
import articleShow from "@/components/articles/show"

export default [{
		path: '/',
		name: 'home',
		component: Home,
	},
	{
		path: '/courses',
		name: 'courses',
		component: Courses,
		//redirect: 'courses'
	},
	{
		path: '/courses/:id/:slug',
		name: 'courseShow',
		component: courseShow,
		alias: '/courses/show/:id/',
		// redirect: '/courses/:id/:slug'
	},
	{
		path: '/questions',
		name: 'questions',
		component: Question
	},
	{
		path: '/questions/:id',
		name: 'questionShow',
		component: questionShow,
		alias: '/questions/show/:id'
	},
	{
		path: '/articles',
		name: 'articles',
		component: Articles
	},
	{
		path: '/articles/:id',
		name: 'articleSshow',
		component: articleShow,
		alias: '/articles/show/:id'
	}


]



