<template>
	<div id="coursesList">
		{{ $route.query.category_id }}
		<div class="product_nav">
			<ul>
				<li v-for="(item, index) in navList" :key="index" :class="{ pro_nav_active: index == num }" @click="coursesTab(index)">
					<router-link :to="{ path: 'courses', query: { category_id: item.id } }">{{ item.name }}</router-link>
				</li>
			</ul>
		</div>
		<div class="product_list">
			<ul>
				<li v-for="pro in proList">
					<router-link :to="{ path: 'courses/' + pro.id + '/' + pro.slug }">
						<div class="product_pic"><img :src="'https://img.xhmwxy.com/' + pro.image" :alt="pro.name" /></div>
						<div class="product_presentation clearfix">
							<div class="pro_pre_word">
								<span>{{ pro.name }}{{ pro.id }}</span>
								<p>报名人数：<span>{{ pro.apply_count }}</span></p>
							</div>
							<div class="pro_pre_icon"><i></i></div>
						</div>
					</router-link>
				</li>
			</ul>
		</div>
		<div class="turn_pages">
			<router-link :to="{ path: 'courses', query: { page: 2 } }">上一页</router-link>
			<template v-for="page in pages.total_pages">
				<router-link @click="coursesPage(index)" :to="{path:'courses',query: {category_id:$route.query.category_id,page: page}}">{{ page }}</router-link>
			</template>
			<a href="javascript:;">下一页</a>
			<a href="javascript:;">共{{ pages.total_pages }}页</a>
		</div>
	</div>
</template>

<script>
export default {
	name: 'coursesList',
	data() {
		return {
			navList: [],
			num: 0,
			proList: [],
			pages: [],
			nextPage: ''
		};
	},
	methods: {
		coursesTab(index) {
			this.num = index;
			index = index + 1;
			// 			console.log(index)
			// 			var thisId = this.$route.params.id;
			// 			console.log(thisId)
			var categoryId = this.$route.query.category_id;

			//1特色小吃
			this.$axios
				.get(this.HOST + 'courses?category_id=' + categoryId, {
					headers: {
						Accept: 'application/prs.meiweixueyuan.v1+json'
					}
				})
				.then(res => {
					this.proList = res.data.data;
					this.pages = res.data.meta.pagination;
					this.nextPage = this.pages.links.next;
					
					console.log(this.nextPage);
				})
				.catch(error => {
					console.log(error);
				});
		},
		coursesPage(index){
			
		}
		
	},
	created() {
		//var categoryId = this.$route.query.category_id;
		//this.getList(categoryId);

		//top 分类
		this.$axios
			.get(this.HOST + '/wap/index/top', {
				headers: {
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then(res => {
				this.navList = res.data.categories;
				for (let i = 0; i < this.navList.length; i++) {
					//console.log(this.navList[i].id)
					this.theId = this.navList[i].id;
				}
				//console.log(this.navList);
			})
			.catch(error => {
				console.log(error);
			});
		//初始列表

		this.$axios
			.get(this.HOST + 'courses?category_id=1', {
				headers: {
					Accept: 'application/prs.meiweixueyuan.v1+json'
				}
			})
			.then(res => {
				this.proList = res.data.data;
				this.pages = res.data.meta.pagination;
				//console.log(this.pages.total_pages)
				//console.log(this.proList);
			})
			.catch(error => {
				console.log(error);
			});
	}
};
</script>

<style></style>



